import axios from "axios";
function sureChangeTool(arr, runner, person, tar, path, preText, testText) {
    axios({
        url: "/surechange",
        method: "post",
        data: {
            "newData": arr, // caseList
            "runner": runner, // 
            "person": person, // 
            "tar": tar, //  
            "path": path,//  
            "preText": preText,// 
            "testText": testText // 
        }
    }).then((res) => {
        if (res) {
            //
            return true
        }
    });
}
export default sureChangeTool;